import axios from 'axios'

const serverUrl = process.env.REACT_APP_ROUTES_SERVER || '/routes'
const aqServiceUrl = process.env.REACT_APP_AQ_SERVER || '/aq'

export interface AqMapDataStatus {
  aq_map_data_available: boolean
  aq_map_data_datetime: number | null
}

export const getAqMapDataStatus = async (): Promise<AqMapDataStatus> => {
  try {
    const response = await axios.get(serverUrl.concat('aq-map-data-status'))
    if (response.data) {
      return response.data as AqMapDataStatus
    }
  } catch (error) {
    console.error('could not fetch AQI map data status')
  }
  return { aq_map_data_available: false, aq_map_data_datetime: null }
}

export const getAqLayerData = async (): Promise<Map<number, number> | undefined> => {
  const response = await axios.get(serverUrl.concat('aq-map-data'))
  console.log(response)
  if (response.data) {
    const dataMap = new Map<number, number>()
    for (const e of response.data.data) {
      dataMap.set(e.id_ig, e['aq']['AQI_european'])
    }
    return dataMap
  } else {
    console.error('no AQI map data available')
  }
}

export async function interpolatePositions(positions: number[][]): Promise<any> {
  const url = `${aqServiceUrl}/interpolate`
  const requestData = {
    positions,
  }

  try {
    const response = await axios.post(url, requestData)
    if (response.status !== 200) {
      throw new Error(`Errore nella richiesta: ${response.status} ${response.statusText}`)
    }

    return response.data
  } catch (error) {
    console.error('Errore durante la richiesta:', error)
    throw error
  }
}
