export type Category = {
  description: string
  color: string
}

export type Pollutant = string

export type RangeMapping = {
  [pollutant: string]: {
    unitOfMeasure: UnitOfMeasure
    ranges: number[] // Array di numeri che rappresentano intervalli consecutivi [minValue1, maxValue1, minValue2, maxValue2, ...]
  }
}

export type UnitOfMeasure = 'g/m' | 'ppm' | 'mg/m' | 'ppb'

export abstract class AirQualitySpec {
  private static instance: AirQualitySpec | null = null

  abstract getCategories(): Category[]
  abstract getRangeMapping(): RangeMapping

  getCategoryInfo(id: number): Category {
    return this.getCategories()[id - 1]
  }

  getCategoryFromValue(pollutant: Pollutant, value: number): Category | undefined {
    const category = this.getCategories()[this.getCategoryIndex(pollutant, value)]
    return category
  }

  private getCategoryIndex(pollutant: Pollutant, value: number): number {
    const rangeInfo = this.getRangeMapping()[pollutant]
    if (!rangeInfo) {
      throw new Error(`Range mapping not found for pollutant: ${pollutant}`)
    }

    const ranges = rangeInfo.ranges

    for (let i = 0; i < ranges.length - 1; i += 1) {
      const minValue = ranges[i]
      const maxValue = ranges[i + 1]

      if (value >= minValue && value <= maxValue) {
        return i / 2
      }
    }

    throw new Error(`Value ${value} does not match any range for pollutant: ${pollutant}`)
  }

  static getInstance(): AirQualitySpec {
    if (AirQualitySpec.instance === null) {
      // Crea l'istanza solo se non esiste gi
      AirQualitySpec.instance = new EuroAQISpec()
    }
    return AirQualitySpec.instance as AirQualitySpec
  }
}

const categories: Category[] = [
  { description: 'Good', color: '#50f0e6' }, // Sostituisci '#HexColor' con il colore esatto
  { description: 'Fair', color: '#50ccaa' },
  { description: 'Moderate', color: '#f0e641' },
  { description: 'Poor', color: '#ff5050' },
  { description: 'Very Poor', color: '#960032' },
  { description: 'Extremely Poor', color: '#7d2181' },
]

// Mappatura degli intervalli per ogni inquinante
const rangeMapping: RangeMapping = {
  'PM2.5': {
    unitOfMeasure: 'g/m',
    ranges: [0, 10, 10, 20, 20, 25, 25, 50, 50, 75, 75, 800],
  },
  PM10: {
    unitOfMeasure: 'g/m',
    ranges: [0, 20, 20, 40, 40, 50, 50, 100, 100, 150, 150, 1200],
  },
  NO2: {
    unitOfMeasure: 'g/m',
    ranges: [0, 40, 40, 90, 90, 120, 120, 230, 230, 340, 340, 1000],
  },
  O3: {
    unitOfMeasure: 'g/m',
    ranges: [0, 50, 50, 100, 100, 130, 130, 240, 240, 380, 380, 800],
  },
  SO2: {
    unitOfMeasure: 'g/m',
    ranges: [0, 100, 100, 200, 200, 350, 350, 500, 500, 750, 750, 1250],
  },
}

export class EuroAQISpec extends AirQualitySpec {

  getCategories(): Category[] {
    return categories
  }

  getRangeMapping(): RangeMapping {
    return rangeMapping
  }
}

