import axios from 'axios'
import { User } from '../types/types'

const API_BASE_URL = process.env.REACT_APP_USERS_SERVER
const LOCALSTORAGE_JWT_KEY = 'ecosister-token'

export const login = async (username: string, password: string): Promise<User | null> => {
  try {
    const response = await axios.post(`${API_BASE_URL}/login`, { username, password }, {
      headers: {
        'Content-Type': 'application/json',
      },
    })

    if (response.status === 200) {
      const data = response.data
      localStorage.setItem(LOCALSTORAGE_JWT_KEY, data.access_token)
      return data.user as User
    } else {
      return null
    }
  } catch (error) {
    console.error('Login error:', error)
    return null
  }
}

export const register = async (username: string, password: string): Promise<boolean> => {
  try {
    const response = await axios.post(`${API_BASE_URL}/register`, { username, password }, {
      headers: {
        'Content-Type': 'application/json',
      },
    })
    return response.status === 201
  } catch (error) {
    console.error('Registration error:', error)
    return false
  }
}

export const getUserData = async (): Promise<User | null> => {
  const jwt = localStorage.getItem(LOCALSTORAGE_JWT_KEY)
  if (jwt !== null || jwt !== undefined) {
    try {
      const response = await axios.get(`${API_BASE_URL}/protected`, {
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })

      if (response.status === 200) {
        const data = response.data
        return data as User
      }
      return null
    } catch (error) {
      console.error('Error fetching user data:', error)
      return null
    }
  } else {
    return null
  }
}

export const logout = (): void => {
  localStorage.removeItem(LOCALSTORAGE_JWT_KEY)
}
