import React, { useEffect, useRef } from 'react'
import { useSelector } from 'react-redux'
import mapboxgl from 'mapbox-gl'
import workIcon from './workIcon.png'

import { ReduxState } from '../../types/types'

type Props = {
  map?: mapboxgl.Map
}

const WorkMarker: React.FC<Props> = ({ map }) => {
  const user = useSelector((state: ReduxState) => state.auth.user)
  const markerRef = useRef<mapboxgl.Marker | null>(null)

  useEffect(() => {
    if (user && user.workPosition && map) {
      const { workPosition } = user

      // Create a custom marker icon using the custom image
      const el = document.createElement('div')
      el.className = 'work-marker'
      el.style.width = '32px' // Adjust the width and height as needed
      el.style.height = '32px'
      el.style.backgroundImage = `url(${workIcon})` // Use the imported custom image
      el.style.backgroundSize = 'cover'

      // Remove the previous marker if it exists
      if (markerRef.current) {
        markerRef.current.remove()
      }

      // Create a new marker and add it to the map
      markerRef.current = new mapboxgl.Marker(el).setLngLat(workPosition).addTo(map)
    }
  }, [user, map])

  return null
}

export default WorkMarker
