import { Component } from 'react'
import { connect, ConnectedProps } from 'react-redux'
import {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setDelayedStyleUpdate,
  setWaitingStyleUpdate,
  setUpdatingStyle,
} from './../../reducers/airQualityLayerReducer'
import { LayerId, MapId } from '../../constants'
import * as aq from '../../services/aq'
import { MbMap, ReduxState } from '../../types/types'
import Streets from '../../streets.json'
import { Feature, FeatureCollection, GeoJsonProperties, LineString } from 'geojson'
import { AirQualitySpec, Category } from '../../types/aqi_spec'

const aqLineColor = [
  'match',
  ['feature-state', 'aq'],
  ...AirQualitySpec.getInstance()
    .getCategories()
    .flatMap((c: Category, id: number) => {
      return [id + 1, c.color]
    }),
  '#CCCCCC',
]

const getUniqueFeatureIds = (features: any[]) => {
  const existingFeatureKeys: Map<number, boolean> = new Map()
  return features
    .map(feat => feat.id)
    .filter(id => {
      if (existingFeatureKeys.has(id)) {
        return false
      } else {
        existingFeatureKeys.set(id, true)
        return true
      }
    })
}

class AirQualityStreetByStreet extends Component<PropsFromRedux & { map?: MbMap }> {
  layerId = LayerId.STREETS_MAP
  sourceId = this.layerId
  data: Map<number, number> | undefined = undefined
  dataTimeUtcSecs: number | null = null

  addStreetsLayer(map: any) {
    if (!map.getLayer(this.layerId)) {
      const streetsFeature = Streets.features
        .filter(f => f.geometry != null)
        .map(f => {
          const res: Feature<LineString> = {
            id: f.properties!.id_ig,
            type: 'Feature',
            properties: f.properties,
            geometry: {
              type: 'LineString',
              coordinates: f.geometry!.coordinates,
            },
          }
          return res
        })

      const s: FeatureCollection<LineString, GeoJsonProperties> = {
        type: 'FeatureCollection',
        features: streetsFeature,
      }

      map!.addSource(this.sourceId, {
        type: 'geojson',
        data: s,
      })

      map!.addLayer({
        id: this.layerId,
        type: 'line',
        source: this.sourceId,
        layout: {},
        paint: {
          'line-width': 1, // Line width for a bold line
        },
      })
    }
  }

  updateAqState = (map: any) => {
    this.props.setUpdatingStyle(true)
    this.props.setWaitingStyleUpdate(false)
    this.addStreetsLayer(map)
    this.props.map!.setPaintProperty(this.layerId, 'line-color', aqLineColor)
    const features = map!.queryRenderedFeatures(undefined, {
      layers: [this.layerId],
    })
    const uniqIds = getUniqueFeatureIds(features)
    // update feature states with new AQI values
    uniqIds.forEach((id, i) => {
      const aq = this.data!.get(id) !== undefined ? this.data!.get(id) : 3
      //const aq = 1
      if (aq) {
        map.setFeatureState(
          {
            source: this.sourceId,
            id: id,
          },
          { aq },
        )
      } else {
        console.log('Missing id for AQ feature')
      }
    })
    console.log('Updated AQIs to feature states')
    this.props.setUpdatingStyle(false)
  }

  /**
   * Triggers AQI map data fetch/update if new or initial AQI data is available.
   */
  maybeUpdateAqData = async (repeat: boolean = true) => {
    if (this.props.mapId === MapId.STREET_BY_STREET && !this.props.layer.loadingData) {
      this.props.setLoadingData(true)
      try {
        this.data = await aq.getAqLayerData()
        this.props.setDelayedStyleUpdate(100)
      } catch (error) {
        console.log(error)
        console.log('error in fetching new AQI map data')
      }
      this.props.setLoadingData(false)
    }
    if (repeat) {
      setTimeout(this.maybeUpdateAqData, 4000)
    }
  }

  componentDidMount = async () => {
    setTimeout(this.maybeUpdateAqData, 400)
    this.props.map!.on('sourcedata', (e: any) => {
      if (
        e.isSourceLoaded &&
        e.sourceId === this.sourceId &&
        this.props.mapId === MapId.STREET_BY_STREET

      ) {
        this.props.setDelayedStyleUpdate(500)
      }
    })
  }

  /**
   * Let's update AQI features' states only after a short delay after either changed map view or new
   * source data download. This way we can avoid unnecessarily updating the feature states multiple
   * times due to single change in map view.
   */
  componentDidUpdate = async (prevProps: PropsFromRedux) => {
    // @ts-ignore - map is given to all children of Map
    const { map } = this.props

    //hidden the layer if the map is not selected
    if (this.props.mapId !== MapId.STREET_BY_STREET) {
      if (map?.getLayer(this.layerId) !== undefined)
        map.setLayoutProperty(this.layerId, 'visibility', 'none')
      return
    }

    //if layer exist and map is selected set visible true
    if (map?.getLayer(this.layerId) !== undefined)
      map.setLayoutProperty(this.layerId, 'visibility', 'visible')

    // add delayed AQI map update to "queue" if basemap changes
    if (prevProps.mapId !== MapId.STREET_BY_STREET) {
      console.log('Changed basemap to AIR QUALITY')
      if (!this.data) {
        await this.maybeUpdateAqData(false)
      } else {
        this.props.setDelayedStyleUpdate(200)
      }
      if (!this.data) {
        this.props.handleDataNotAvailable()
      }
    }

    // add delayed AQI map update to "queue" if map center changes
    if (JSON.stringify(prevProps.mapCenter) !== JSON.stringify(this.props.mapCenter) && this.data) {
      this.props.setDelayedStyleUpdate(1300)
    }

    // implement the AQI style update if and only after all update delays have passed
    if (
      this.props.layer.waitingStyleUpdate &&
      this.props.layer.styleUpdateDelays === 0 &&
      prevProps.layer.styleUpdateDelays > 0 &&
      this.props.map &&
      this.data
    ) {
      // trigger AQI map style update if necessary
      this.updateAqState(this.props.map)
    }
  }

  render() {
    return null
  }
}

const mapStateToProps = (state: ReduxState) => ({
  mapId: state.map.mapId,
  layer: state.airQualityLayer,
  mapCenter: state.map.center,
})

const connector = connect(mapStateToProps, {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setWaitingStyleUpdate,
  setUpdatingStyle,
  setDelayedStyleUpdate,
})
type PropsFromRedux = ConnectedProps<typeof connector>
export default connector(AirQualityStreetByStreet)
