import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { ReduxState } from '../../types/types'
import { logoutSuccess, showLoginModal } from '../../reducers/authReducer'
import { logout } from '../../services/auth'

const UserStatus: React.FC = () => {
  const user = useSelector((state: ReduxState) => state.auth.user)
  const dispatch = useDispatch()

  const handleLogout = () => {
    logout() // Chiamata al servizio di logout (potrebbe essere asincrona)
    dispatch(logoutSuccess()) // Aggiorna lo stato Redux
  }

  const handleLogin = () => {
    dispatch(showLoginModal())
  }

  return (
    <div>
      {user ? (
        <div>
          <span onClick={handleLogout}>{user.username}</span>
        </div>
      ) : (
        <span onClick={handleLogin}>login/register</span>
      )}
    </div>
  )
}

export default UserStatus
