import { Component } from 'react'
import { connect, ConnectedProps } from 'react-redux'
import mapboxgl from 'mapbox-gl'
import sensorIcon from './sensorIcon.png'


import {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setDelayedStyleUpdate,
  setWaitingStyleUpdate,
  setUpdatingStyle,
} from './../../reducers/airQualityLayerReducer'
import { MbMap, ReduxState } from '../../types/types'

type Sensor = {
  AQI_american: number
  AQI_european: number
  name: string
  pollutants: {
    CO: number
    NO2: number
    O3: number
    PM10: number
    'PM2.5': number
    SO2: number
  }
  position: [number, number]
}

const mapStateToProps = (state: ReduxState) => ({
  basemap: state.map.basemap,
  layer: state.airQualityLayer,
  mapCenter: state.map.center,
})

const mapDispatchToProps = {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setWaitingStyleUpdate,
  setUpdatingStyle,
  setDelayedStyleUpdate,
}

const connector = connect(mapStateToProps, mapDispatchToProps)
type PropsFromRedux = ConnectedProps<typeof connector>

type Props = PropsFromRedux & {
  map?: MbMap
}

class AirQualitySensors extends Component<Props> {
  intervalId: number | null = null

  componentDidMount() {
    this.fetchSensorsData()
    this.intervalId = window.setInterval(this.fetchSensorsData, 60000)
  }

  componentWillUnmount() {
    if (this.intervalId !== null) {
      window.clearInterval(this.intervalId)
    }
  }

  fetchSensorsData = async () => {
    try {
      this.props.setLoadingData(true)
      const response = await fetch(`${process.env.REACT_APP_AQ_SERVER}/sensors`)
      if (!response.ok) throw new Error('Network response was not ok')
      const data = await response.json()
      this.displaySensorsOnMap(data.sensors)
    } catch (error) {
      console.error('There has been a problem with your fetch operation:', error)
      this.props.handleDataNotAvailable()
    } finally {
      this.props.setLoadingData(false)
    }
  }

  displaySensorsOnMap = (sensors: Sensor[]) => {
    const { map } = this.props
    if (!map) return

    sensors.forEach((sensor) => {
      const el = document.createElement('div')
      el.className = 'home-marker'
      el.style.width = '32px' // Adjust the width and height as needed
      el.style.height = '32px'
      el.style.backgroundImage = `url(${sensorIcon})` // Use the imported custom image
      el.style.backgroundSize = 'cover'

      // Crea il contenuto del Popup
      const description = `<strong>${sensor.name}</strong><br>` +
        `AQI US: ${sensor.AQI_american}, AQI EU: ${sensor.AQI_european}<br>` +
        Object.entries(sensor.pollutants).map(([key, value]) => `${key}: ${value}`).join('<br>')

      // Crea il Popup
      const popup = new mapboxgl.Popup({ offset: 25 })
        .setHTML(description)

      // Aggiungi il marker e il popup alla mappa
      new mapboxgl.Marker(el)
        .setLngLat(sensor.position)
        .setPopup(popup) // Collega il Popup al Marker
        .addTo(map)
    })
  }

  render() {
    return null
  }
}

export default connector(AirQualitySensors)
